import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import Drink from "./Component/drink";
import Food from "./Component/food";
import Kasir from "./Component/kasir";
import Header from "./Master/header";
import Profile from "./Master/Profile";
import { HiHome } from "react-icons/hi";
import { MdGroup } from "react-icons/md";
import { BiFoodMenu } from "react-icons/bi";
import { BiDrink } from "react-icons/bi";
import { BiShoppingBag } from "react-icons/bi";
import {
  BrowserRouter,
  Router,
  Switch,
  Route,
  NavLink,
  Routes,
} from "react-router-dom";
const Routing = () => {
  return (
    <BrowserRouter>
      <div>
        <Header />

        <Routes>
          <Route exact path="/" element={<App />}></Route>
          <Route path="/food" element={<Food />}></Route>
          <Route path="/drink" element={<Drink />}></Route>
          <Route path="/kasir" element={<Kasir />}></Route>
          <Route path="/profile" element={<Profile />}></Route>
        </Routes>

        <footer>
          <NavLink to="/" className="iconWrapper">
            <HiHome className="icon" />
            Home
          </NavLink>

          <NavLink to="/food" className="iconWrapper">
            <BiFoodMenu className="icon" />
            Food
          </NavLink>

          <NavLink to="/drink" className="iconWrapper">
            <BiDrink className="icon" />
            Drink
          </NavLink>

          <NavLink to="/kasir" className="iconWrapper">
            <BiShoppingBag className="icon" />
            Buy
          </NavLink>

          <NavLink to="/profile" className="iconWrapper">
            <MdGroup className="icon" />
            Profile
          </NavLink>
        </footer>
      </div>
    </BrowserRouter>
  );
};

ReactDOM.render(
  <React.StrictMode>
    <Routing />
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
