import "./ProfileCard.css";
import React from "react";

import { FaInstagram, FaGithub } from "react-icons/fa";

export default function ProfileCard({ name, nim, github, ig, image }) {
  return (
    <div className="card">
      <div className="center">
        <img className="image" alt="" src={image} />
        <p id="name">{name}</p>
        <p id="nim">{nim}</p>
        <button
          className="igbtn"
          onClick={(e) => {
            e.preventDefault();
            window.open(ig, "_blank");
          }}
        >
          <FaInstagram />
        </button>
        <button
          className="githubbtn"
          onClick={(e) => {
            e.preventDefault();
            window.open(github, "_blank");
          }}
        >
          <FaGithub />
        </button>
      </div>
    </div>
  );
}
